@h: 200px;
@trans: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);

.harga {
  z-index: 1;
  transition: @trans;
}

div{
  &[class*=col-] {
    transition: @trans;
    background-color: initial;

    >:hover {
      cursor: pointer;
      background-color: hsla(39, 97%, 49%, 0.13);

      .photo {
        background-color: hsla(0, 0%, 0%, 0.25);
      }

      .harga {
        font-size: xx-large;
        top: @h * .35;
        right: @h * .25;
      }

      img {
        opacity: .25;
        filter: blur(2px);
      }
    }
  }

  &.photo {
    height: @h;
    transition: @trans;

    img {
      transition: @trans;
      height: @h;
    }
  }
}

ul {
  list-style-type: upper-alpha;
}
